@include option("tables") {

[class*="Table"],  [class*="Afb"] table, {
	width: 100%;
	max-width: 100%;
	table-layout: auto;
	border-spacing: 0px;
	border: $table-border-width solid var(--table-border-color);
	border-radius: $table-border-radius;
    margin-top: $table-margin-top;
    margin-bottom: $table-margin-bottom;
    background: var(--table-background);

	@if $table-border-radius > 0 {
		border-collapse: separate;

		td, th {
			border-right: $table-border-width solid var(--table-cell-border-color);
			border-bottom: $table-border-width solid var(--table-cell-border-color);

			&:last-child {
				border-right: none;
			}
		}

		tbody tr:last-child {
			td, th {
				border-bottom: none;
			}
		}

		tfoot {
			td, th {
				border-top: $table-border-width solid var(--table-cell-border-color);
				border-bottom: none;
			}
		}
	}
	@else {
		border-collapse: collapse;

		td, th {
			border: $table-border-width solid var(--table-cell-border-color);
		}
	}

	td, th {
		padding: $table-cell-padding;
	}

	th {
		background: var(--table-headings-cell-background);
	}

	&[class*="--fixed"] {
		table-layout: fixed;
	}

	&[class*="--slim"] {
		td, th {
			padding: $table-slim-cell-padding;
		}
	}

	&[class*="--zebra"] {
		th {
			background: var(--table-zebra-headings-cell-background);
		}

		tr:nth-child(even) {
			background: var(--table-zebra-cell-background);
		}
	}

	&[class*="--hover"] {
		th {
			background: var(--table-headings-cell-background);
		}

		tbody tr {
            @include transition("background");
            &:hover {
                background: var(--table-cell-hover-background);
            }
		}
	}

	&[class*="--light"] {
		background: $table-background;
		color: $body-color;

		th {
			background: $table-headings-cell-background;
		}

		&[class*="--zebra"] {
			tr:nth-child(even) {
				background: $table-zebra-cell-background;
			}
		}

		&[class*="--hover"] tbody tr {
            &:hover {
                background: $table-cell-hover-background;
            }
        }
	}

	&[class*="--dark"] {
		background: $table-background-dark;
		color: $body-color-dark;

		th {
			background: $table-heading-cell-background-dark;
		}

		&[class*="--zebra"] {
			tr:nth-child(even) {
				background: $table-zebra-cell-background-dark;
			}
		}

		&[class*="--hover"] tbody tr {
            &:hover {
                background: $table-cell-hover-background-dark;
            }
        }

        a {
            color: $link-color-dark;

            &:hover {
                color: $link-hover-color-dark;
            }
        }
	}

	caption {
		color: var(--table-caption-color);
		font-style: $table-caption-font-style;
	}
}

}
